class SuplinkSDK {
  loginInfo: loginInfo;

  userInfo: UserInfo | {};

  getSystemInfo: typeof getSystemInfo;

  getNetworkInfo: typeof getNetworkInfo;

  filePreview: typeof filePreview;

  navBack: typeof navBack;

  ocr: typeof ocr;

  login: typeof login;

  navConfig: typeof navConfig;

  upload: typeof upload;

  arrayBufferToBase64: typeof arrayBufferToBase64;

  base64ToArrayBuffer: typeof base64ToArrayBuffer;

  scanCode: typeof scanCode;

  renderFinish: typeof renderFinish;

  departSelect: typeof departSelect;

  getUserInfo: typeof getUserInfo;

  personnelSelector: typeof personnelSelector;

  onAppShow: typeof onAppShow;

  onAppHide: typeof onAppHide;

  offAppShow: typeof offAppShow;

  offAppHide: typeof offAppHide;

  gesture: typeof gesture;

  record: typeof record;

  nfc: typeof nfc;

  getLocation: typeof getLocation;

  startLocationUpdate: typeof startLocationUpdate;

  stopLocationUpdate: typeof stopLocationUpdate;

  getStorage: typeof getStorage;

  setStorage: typeof setStorage;

  launchApp: typeof launchApp;

  signature: typeof signature;

  launchMiniProgram: typeof launchMiniProgram;

  biometrics: typeof biometrics;

  getStepCount: typeof getStepCount;

  compass: typeof compass;

  flashlight: typeof flashlight;

  makePhoneCall: typeof makePhoneCall;

  constructor() {
    this.getSystemInfo = getSystemInfo;
    this.getNetworkInfo = getNetworkInfo;
    this.filePreview = filePreview;
    this.navBack = navBack;
    this.ocr = ocr;
    this.login = login;
    this.navConfig = navConfig;
    this.upload = upload;
    this.scanCode = scanCode;
    this.renderFinish = renderFinish;
    this.departSelect = departSelect;
    this.getUserInfo = getUserInfo;
    this.personnelSelector = personnelSelector;
    this.arrayBufferToBase64 = arrayBufferToBase64;
    this.base64ToArrayBuffer = base64ToArrayBuffer;
    this.onAppShow = onAppShow;
    this.onAppHide = onAppHide;
    this.offAppHide = offAppHide;
    this.offAppShow = offAppShow;
    this.gesture = gesture;
    this.record = record;
    this.nfc = nfc;
    this.getLocation = getLocation;
    this.startLocationUpdate = startLocationUpdate;
    this.stopLocationUpdate = stopLocationUpdate;
    this.getStorage = getStorage;
    this.setStorage = setStorage;
    this.launchApp = launchApp;
    this.signature = signature;
    this.launchMiniProgram = launchMiniProgram;
    this.biometrics = biometrics;
    this.getStepCount = getStepCount;
    this.compass = compass;
    this.flashlight = flashlight;
    this.makePhoneCall = makePhoneCall;
    this.userInfo = {};
    this.loginInfo = {
      userName: '',
      token: '',
      supostoken: '',
      appId: '',
      baseUrl: '',
      appName: '',
    };
  }
}

const Suplink = new SuplinkSDK();

export const EventChannel = EventInstance;

if (isWeb()) {
  document.addEventListener('visibilitychange', function visibilitychange(e) {
    const { __events } = EventChannel;
    let funcs: any[] = [];
    if (document.visibilityState === 'visible') {
      funcs = __events.filter((event) => event.onAppShow);
    } else {
      funcs = __events.filter((event) => event.onAppHide);
    }

    if (Array.isArray(funcs)) {
      funcs.forEach((func) =>
        func(e, {
          path: window.location.href,
          query: parse(window.location.search),
        })
      );
    }
  });
}

export default Suplink;
